import React from "react";
import axios from "axios";

function PostDelete(e, id) {
	const deletePost = async () => {
		const res = await axios.delete(`http://localhost:3001/posts/${id}`)
			.then(res => {
				alert("  .");
			})
			.catch(err => {
				alert("   .");
			});
	}
	if (window.confirm("  ?") === true)
		deletePost();
	else
		return;
}

export default PostDelete;
