

function GetTime(before) {
	let time = ((Date.now() - Date.parse(before)) / 1000) / 60; // 
	time = Math.floor(time); // Date.now updatedAt  18 .
	if (time < 1)
		return " ";
	else if (time < 60)
		return `${Math.floor(time)} `;
	else if (time < 1440)
		return `${Math.floor(time / 60)} `;
	else if (time < 43200)
		return `${Math.floor(time / 60 / 24)} `;
	else if (time < 525600)
		return `${Math.floor(time / 60 / 24 / 30)} `;
	else
		return `${Math.floor(time / 60 / 24 / 30 / 12)} `;
}

export default GetTime;
